/* =========================
   GOOGLE FONTS
   ========================= */
@import url("https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700;900&family=Libre+Baskerville:wght@400;700&display=swap");

.sable-header {
  width: 100%;
}

/* =========================
   TOP BLUE BAR
   ========================= */
.topbar {
  height: 64px;
  background: #0b3f87;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 18px;
  gap: 18px;
}

.topbar-left {
  display: flex;
  align-items: center;
  gap: 18px;
  min-width: 0;
}

/* =========================
   BRAND (LOGO + TITLE)
   ========================= */
.brand {
  display: flex;
  align-items: center;
  gap: 10px;
  user-select: none;
  border: 0;
  background: transparent;
  cursor: pointer;
  padding: 6px 8px;
  border-radius: 10px;
}

.brand:hover {
  background: rgba(255, 255, 255, 0.08);
}

.brand-logo {
  width: 26px;
  height: 26px;
  object-fit: contain;
  display: block;
}

.brand-text {
  color: #f7f1e6;
  font-family: "Cinzel Decorative", serif;
  font-weight: 700;
  font-size: 20px;
  letter-spacing: 3px;
  line-height: 1;
}

/* =========================
   TOP NAV LINKS
   ========================= */
.topnav {
  display: flex;
  align-items: center;
  gap: 14px;
  color: #f7f1e6;
  font-family: "Libre Baskerville", serif;
  font-size: 15px;
  white-space: nowrap;
}

.navlink {
  color: #f7f1e6;
  opacity: 0.9;
}

.navlink:hover {
  opacity: 1;
  text-decoration: underline;
  text-underline-offset: 6px;
}

.navlink.active {
  opacity: 1;
  font-weight: 700;
}

.divider {
  width: 2px;
  height: 18px;
  background: #d7b24c;
  border-radius: 2px;
}

/* =========================
   RIGHT SIDE / ICONS
   ========================= */
.topbar-right {
  display: flex;
  align-items: center;
  gap: 12px;
  color: #f7f1e6;
  flex-shrink: 0;
}

.iconbar {
  display: flex;
  align-items: center;
  gap: 8px;
}

.iconbtn {
  width: 34px;
  height: 34px;
  border: 0;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.12);
  cursor: pointer;
  display: grid;
  place-items: center;
  padding: 0;
}

.iconbtn:hover {
  background: rgba(255, 255, 255, 0.18);
}

.iconbtn--svg .navIcon {
  width: 18px;
  height: 18px;
  display: block;
  object-fit: contain;
}

.loginBtn {
  border: 1px solid rgba(255, 255, 255, 0.35);
  background: rgba(255, 255, 255, 0.14);
  color: #f7f1e6;
  padding: 8px 14px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  font-family: "Libre Baskerville", serif;
}

.loginBtn:hover {
  background: rgba(255, 255, 255, 0.18);
}

/* =========================
   USER DROPDOWN
   ========================= */
.userMenu {
  position: relative;
}

.userButton {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  border: 0;
  background: transparent;
  color: #f7f1e6;
  cursor: pointer;
  padding: 6px 8px;
  border-radius: 10px;
}

.userButton:hover {
  background: rgba(255, 255, 255, 0.10);
}

.greetingText {
  font-size: 14px;
  opacity: 0.95;
  white-space: nowrap;
  font-family: "Libre Baskerville", serif;
}

.chev {
  opacity: 0.85;
  transform: translateY(1px);
}

/* =========================
   DROPDOWN MENU
   ========================= */
.dropdown {
  position: absolute;
  right: 0;
  top: calc(100% + 10px);
  width: 220px;
  background: #f4efe6;
  border: 1px solid rgba(0, 0, 0, 0.14);
  border-radius: 12px;
  box-shadow: 0 18px 55px rgba(0, 0, 0, 0.35);
  padding: 8px;
  z-index: 5000;
}

.dropItem {
  width: 100%;
  text-align: left;
  border: 0;
  background: transparent;
  padding: 10px 10px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 700;
  color: #1b1b1b;
  font-family: "Libre Baskerville", serif;
}

.dropItem:hover {
  background: rgba(11, 63, 135, 0.10);
}

.dropItem.danger {
  color: #7a0d12;
}

.dropItem.danger:hover {
  background: rgba(122, 13, 18, 0.10);
}

.dropDivider {
  height: 1px;
  background: rgba(0, 0, 0, 0.10);
  margin: 6px 0;
}

/* =========================
   SECONDARY GREEN BAR
   ========================= */
.subbar {
  background: #244b2b;
  padding: 10px 18px;
}

.browseForm {
  position: relative;
  max-width: 760px;
}

.searchIcon {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.75;
}

.browseInput {
  width: 100%;
  height: 34px;
  padding: 0 12px 0 40px;
  border-radius: 999px;
  border: 0;
  outline: none;
  background: #ffffff;
  font-family: "Libre Baskerville", serif;
}

/* =========================
   LOGIN / SIGNUP MODAL
   ========================= */
.loginOverlay {
  position: fixed;
  inset: 0;
  background: rgba(35, 25, 25, 0.55);
  display: grid;
  place-items: center;
  padding: 20px;
  z-index: 9999;
}

.loginCard {
  width: 100%;
  max-width: 440px;
  max-height: calc(100vh - 40px);
  overflow: auto;

  border-radius: 14px;
  background: #d99621;
  border: 3px solid rgba(255, 220, 140, 0.85);
  box-shadow: 0 20px 55px rgba(0, 0, 0, 0.45);
  padding: 18px 22px 22px;
  position: relative;
  text-align: center;
}

.loginClose {
  position: absolute;
  right: 12px;
  top: 12px;
  width: 32px;
  height: 32px;
  border-radius: 999px;
  border: 2px solid rgba(80, 45, 20, 0.5);
  background: rgba(255, 215, 140, 0.55);
  cursor: pointer;
  font-size: 16px;
  line-height: 1;
  display: grid;
  place-items: center;
}

.loginClose:hover {
  filter: brightness(1.05);
}

.loginGraphic {
  width: 180px;
  max-width: 65%;
  height: auto;
  margin: 6px auto 8px;
  display: block;
}

.loginTitle {
  margin: 6px 0 6px;
  font-family: "Cinzel Decorative", serif;
  font-size: 24px;
  letter-spacing: 2px;
  color: #1d1208;
  text-transform: uppercase;
}

.loginSub {
  margin: 0 0 14px;
  font-family: "Libre Baskerville", serif;
  font-size: 16px;
  line-height: 1.35;
  color: rgba(255, 255, 255, 0.92);
  font-weight: 400;
}

.googleBtn {
  width: 100%;
  max-width: 340px;
  height: 44px;
  margin: 4px auto 12px;
  border-radius: 8px;
  border: 0;
  background: #ffffff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-family: "Libre Baskerville", serif;
  font-size: 15px;
  font-weight: 700;
}

.googleBtn:hover {
  filter: brightness(0.98);
}

.googleDot {
  width: 24px;
  height: 24px;
  border-radius: 999px;
  background: #1c8f3a;
  color: #ffffff;
  display: grid;
  place-items: center;
  font-weight: 900;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

.googleText {
  color: #222;
}

.authSwitch {
  margin: 2px 0 12px;
  font-family: "Libre Baskerville", serif;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.92);
}

.authLink {
  border: 0;
  background: transparent;
  color: rgba(255, 255, 255, 0.98);
  font-weight: 700;
  cursor: pointer;
  padding: 0;
  font-family: "Libre Baskerville", serif;
  font-size: 14px;
  text-decoration: underline;
  text-underline-offset: 4px;
}

.authLink:hover {
  filter: brightness(1.05);
}

.loginForm {
  width: 100%;
  max-width: 360px;
  margin: 0 auto;
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.loginField {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.loginLabel {
  font-family: "Libre Baskerville", serif;
  font-size: 13px;
  font-weight: 700;
  color: rgba(255, 255, 255, 0.95);
}

.loginInput {
  height: 42px;
  border-radius: 9px;
  border: 0;
  outline: none;
  padding: 0 12px;
  background: #ffffff;
  font-family: "Libre Baskerville", serif;
  font-size: 14px;
}

.loginInput:focus {
  box-shadow: 0 0 0 3px rgba(255, 235, 200, 0.35);
}

.authError {
  margin-top: 2px;
  background: rgba(125, 15, 18, 0.14);
  border: 1px solid rgba(125, 15, 18, 0.25);
  color: #3a0b0d;
  border-radius: 10px;
  padding: 10px 10px;
  font-size: 13px;
  font-weight: 700;
  text-align: center;
  font-family: "Libre Baskerville", serif;
}

.loginPrimary {
  height: 46px;
  border-radius: 9px;
  border: 0;
  background: rgba(255, 255, 255, 0.18);
  color: rgba(255, 255, 255, 0.95);
  font-weight: 800;
  cursor: pointer;
  font-size: 14px;
  margin-top: 6px;
  font-family: "Libre Baskerville", serif;
}

.loginPrimary:hover {
  filter: brightness(1.05);
}

.loginNote {
  margin: 6px 0 0;
  font-size: 11px;
  opacity: 0.75;
  color: rgba(0, 0, 0, 0.7);
  text-align: center;
  font-family: "Libre Baskerville", serif;
}

/* =========================
   BROWSE DROPDOWN (REFINED SIZE)
   ========================= */

.browseWrap {
  position: relative;
  max-width: 760px;
}

.browseDropdown {
  position: absolute;
  left: 0;
  top: calc(100% + 10px);

  width: 380px;              /*  was 520 */
  max-width: 90vw;

  background: rgba(243, 239, 231, 0.96);
  border: 2px solid rgba(214, 168, 64, 0.9); /*  thinner */
  border-radius: 14px;
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.28); /*  lighter */

  padding: 12px 12px 10px;   /*  tighter */
  z-index: 6000;
}

.browseDropItem {
  width: 100%;
  height: 36px;              /*  was 44 */
  border-radius: 999px;
  border: 0;
  background: rgba(255, 255, 255, 0.85);

  text-align: left;
  padding: 0 14px;

  font-family: "Libre Baskerville", serif;
  font-size: 14px;           /*  was 20 */
  font-weight: 700;
  color: #1b1b1b;

  cursor: pointer;
}

.browseDropItem + .browseDropItem {
  margin-top: 8px;           /*  tighter spacing */
}

.browseDropItem:hover {
  filter: brightness(0.97);
}

.browseDropItem:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(214, 168, 64, 0.35);
}














